	module.exports = {"data":{"markdownRemark":{"html":"<p>JumpStart Georgia requested data about the national Georgian budget from the Georgian government in 2016. We received spreadsheets and PDFs containing valuable, but disconnected, financial data.</p>\n<p>As the lead developer on this project, I organized the financial data and created a website visualizing the data. I chose to separate the frontend (React/Redux) from the backend (Rails), which contains the API and the data uploader.</p>\n<p>If you're interested in contributing to this project, here are the relevant repositories:</p>\n<ul>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-API\">Backend/API</a></li>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-Public\">Public Site</a></li>\n<li><a href=\"https://github.com/JumpStartGeorgia/Georgian-Budget-Files\">Files</a></li>\n</ul>","frontmatter":{"slug":"georgian_budget","title":"Georgian Budget","date":"January 1, 2017","tags":"open-source website jumpstart-georgia ruby rails javascript react","mainUrl":"https://biujeti.jumpstart.ge","githubUrl":"https://github.com/JumpStartGeorgia/Georgian-Budget-Public","featuredImage":{"childImageSharp":{"responsiveSizes":{"src":"/static/Georgian-National-Budget-f361d6f7bbe25f8c698a06313350b3d4-30e0b.png","srcSet":"/static/Georgian-National-Budget-f361d6f7bbe25f8c698a06313350b3d4-665be.png 360w,\n/static/Georgian-National-Budget-f361d6f7bbe25f8c698a06313350b3d4-431d3.png 720w,\n/static/Georgian-National-Budget-f361d6f7bbe25f8c698a06313350b3d4-30e0b.png 1440w","sizes":"(max-width: 1440px) 100vw, 1440px"}}}}}},"pathContext":{"slug":"georgian_budget"}}

